header{
    height:100vh;
    padding-top:1rem;
    overflow: hidden;
}

.header__container{
    text-align:center;
    height:100%;
    position:relative;   
    
}

.cta{
    margin-top: 2.5rem;
    display:flex;
    gap:1.2rem;
    justify-content: center;
}


.header__social{
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 0.8rem;
    position:absolute;
    left:0;
    bottom: 3rem;
}
.header__social::after{
    content:"";
    width:1px;
    height:2rem;
    background: var(--color-primary);
}
.image{
    background: linear-gradient(var(--color-primary),transparent);
    overflow: hidden;
    width:22rem;
    height:30rem;
    position:absolute;   
    border-radius: 12rem 12rem 0 0;
    padding: 5rem 1.5rem 1.5rem 1.5rem;

    margin-top: 4rem;
    left:calc(50% - 11rem);
 
}
img{
    width:100%;
    height: 100%;
    object-fit:cover;
}

.scroll__down{
    position:absolute;
    right:-2.3rem;
    bottom:5rem;
    transform:rotate(90deg);
    font-weight:300;
    font-size: 0.9rem;
}
 /* MEDIUM */
@media screen and (max-width:1024px) {
    header{
        height :68vh;
      
    }
    
}

@media screen and (max-width:600px) {
    header{
        height: 100vh;
        padding-top:2rem; 
    }
    header h1{
        font-size: 1.5rem; 
    }
    .header__social, .scroll__down{
     display: none;
    }
    
}