nav{
    background: rgba(0,0,0,0.3);
    width:max-content;
    display: block;
    z-index: 2;
    position: fixed;
    left:50%;
    transform: translateX(-50%);
    bottom:2rem;
    display: flex;
    gap:0.8rem;
    border-radius: 3rem;
    backdrop-filter: blur(20px);
    padding:0.5rem;
}

nav a {
    background:transparent;
    padding: 0.9rem;
    border-radius: 50%;
    display: flex;
    color:var(--color-light);
    font-size: 1.1rem;
}

nav a:hover{
    background: rgba(0,0,0,0.3);
}

nav a.active {
    background: rgba(0,0,0,0.3);
    color:var(--color-white);
}
